}
.rollbar li:hover h6 {
    right: 100%;
}
.rollbar h6 {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: -15%;
    margin: -13px 10px 0 0;
    line-height: 26px;
    font-size: 12px;
    background-color: #666;
    color: #fff;
    width: 53px;
    border-radius: 2px;
    text-align: center;
    opacity: 0;
    filter: alpha(opacity=0);
