.sign {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    /*background-color:rgba(48,58,65,0.9);*/
    z-index: 999;
    -webkit-perspective: 500px;
    -ms-perspective: 500px;
    -o-perspective: 500px;
    perspective: 500px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    opacity: 0.0;
    visibility: hidden;
    overflow: auto;
}
.sign-mask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #313B43;
    opacity: 0.90;
    filter: alpha(opacity=90);
}
.sign .container {
    background-color: #e7ebef;
    width: 400px;
    position: absolute;
    height: 360px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9;
    margin: auto;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: rotateX(-20deg) translateZ(200px) translateY(20%);
    transform: rotateX(-20deg) translateZ(200px) translateY(20%);
}
.sign .close-link {
    color: #fff;
    position: absolute;
    left: 100%;
    top: 0;
    padding: 22px 0;
    font-size: 18px;
    background-color: #61B3E6;
    display: block;
    opacity: 0.85;
    filter: alpha(opacity=85);
    width: 70px;
    text-align: center;
}
.sign .close-link:hover {
    color: #fff;
    opacity: 1;
    filter: alpha(opacity=100);
}
.sign form {
    padding: 0 50px;
}
.sign h3 {
    margin: 0 -50px 20px;
    background-color: #fff;
    padding: 25px 50px;
    font-size: 18px;
    color: #61B3E6;
}
.sign h3 small {
    float: right;
    cursor: pointer;
    color: #bbb;
    font-size: 14px;
    margin-top: 4px;
}
.sign h6 {
    margin-bottom: 15px;
}
.sign .sign-submit .btn {
    float: right;
}
.sign .sign-submit label {
    margin-top: 8px;
    font-weight: normal;
    font-size: 12px;
}
.sign .sign-submit label input {
    position: relative;
    top: 2px;
    margin-right: 4px;
}
.sign-tips {
    position: absolute;
    bottom: 0;
    background-color: #FD6E6E;
    left: 0;
    right: 0;
    height: 0;
    line-height: 29px;
    padding: 0 50px;
    color: #fff;
    font-size: 12px;
    overflow: hidden;
}
.sign-show .sign {
    opacity: 1.0;
    visibility: visible;
}
.sign-show .sign .container {
    -webkit-transform: rotateX(-0deg) translateZ(0) translateY(0%);
    transform: rotateX(-0deg) translateZ(0) translateY(0%);
}
