    right: 0;
    top: 0;
    width: 56px;
    height: 56px;
    text-align: center
}
.zs-head .author {
    padding-top: 15px;
    display: block
}
.zs-head .author img {
    margin-right: 5px
}
.zs-head .tip {
    background: #f6f6f6;
    padding: 0 10px;
    font-size: 12px;
    color: gray;
    border-radius: 3px;
    margin-top: 10px;
    position: relative;
    line-height: 28px
}
.zs-head .tip i {
    display: block;
    border-width: 0 7px 7px 7px;
    border-color: transparent transparent #f6f6f6 transparent;
    border-style: dashed dashed solid dashed;
    content: "";
    position: absolute;
    left: 8px;
    top: -7px
}
.zs-body {
    padding: 0 15px
}
.zs-btns {
    font-size: 0
}
.zs-btns .btn:nth-child(3n-2) {
    margin-left: 0
}
.zs-btns .btn {
    width: 28%;
    margin-left: 8%;
    margin-bottom: 24px;
    display: inline-block;
    font-size: 12px
}
.zs-btns .btn-blink {
    background: #fff;
    border-color: #e26d6d;
    color: #e26d6d;
    font-weight: bold
}
.zs-btns .btn-blink:hover, .zs-btns .btn-blink:active {
    background: rgba(226, 109, 109, .06);
    -webkit-box-shadow: none;
            box-shadow: none
}
.zs-btns .btn-blink:focus {
    background: #e26d6d;
    border-color: #e26d6d;
    color: #fff
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
.zs-body ul {
        letter-spacing: -5px
}
}
.zs-footer {
    padding: 0 15px 15px
}
.zs-footer label {
    margin: 0 10px
}
.zs-footer img {
    height: 30px;
    min-width: 85px
}
.zs-pay {
    text-align: center;
    padding: 15px 0;
    display: none
}
.zs-pay .btn-red {
    min-width: 80px
}
.widget-area .widget-jiangtang ul li {
    margin-bottom: 10px;
    overflow: hidden;
    position: relative
}
.widget-jiangtang li img {
    float: left;
    width: 180px;
    margin-right: 20px;
    border-radius: 3px;
    border: 1px solid #ddd
}
.widget-jiangtang .tit {
    font-size: 14px;
    color: #333;
    font-weight: bold;
    line-height: 26px;
    height: 52px;
    overflow: hidden;
    display: block
}
.widget-jiangtang span {
    display: block;
    line-height: 24px
}
.widget-jiangtang .join {
    font-size: 14px
}
.widget-jiangtang em {
    color: #f00
}
.hentry .yarpp-related .widget-title {
    background: #f5f7f8;
    margin-left: -20px;
    padding-left: 20px;
    font-size: 16px;
    border-left: 5px solid #428bca;
    line-height: 40px
}
.hentry .yarpp-related .r-tit {
    margin: 0 0 5px;
    font-size: 15px
}
.hentry .yarpp-related ul li {
    list-style: square;
    color: #999
}
.hentry .yarpp-related ul li:nth-child(2) .r-tit {
    font-weight: bold
}
.hentry .yarpp-related ul li:nth-child(4) .r-tit {
    text-decoration: underline
}
.hentry .yarpp-related .r-info {
    font-size: 14px;
    line-height: 1.5;
    height: 22px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #999;
    margin-bottom: 10px
}
.middle-box {
    height: 80px;
    text-align: center;
    background: #fff
}
.middle-box p {
    vertical-align: middle;
    display: inline-block;
    *display: inline;
    *zoom: 1;
}
.middle-box p a {
    font-size: 12px;
    padding: 10px;
    display: block;
    font-weight: bold
}
.middle-box p a:hover {
    text-decoration: underline
}
.visible {
    height: 100%;
    vertical-align: middle;
    width: 0;
    display: inline-block
}
.aliyun-inner {
    margin: 15px 0;
    overflow: hidden;
    text-align: center;
    border: 1px solid #ddd;
    font-size: 0
}
.aliyun-inner .middle-box {
    width: 33.3%;
    display: inline-block
}
.entry-meta{
    display:none;
}
.entry-header div:nth-child(2){
    display:none;
}
.entry-details{
    display:none;
}
.relatedposts{
    display:none;
}
.btn-group,.vote-group{
    display:none;
}
h2,h3,h4,h5,p,li{
    text-align:left;
}
